import React from "react";
import { connect } from "react-redux";
import axios from "axios";
import {HOST_URL} from "../settings";
import {Button, Dropdown, Icon, Image, List, Search} from "semantic-ui-react";
import {logout} from "../store/actions/auth";


const rolesAdapt = {
  'streamer': '–°—Ç—Ä–∏–º–µ—Ä',
  'administrator': '–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä—ã',
  'moderator': '–ú–æ–¥–µ—Ä–∞—Ç–æ—Ä—ã',
  'vip': 'VIP',
  'user': '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏',
}

class ChatHeader extends React.Component {
  state = {
    loading: true,
    usersList: false,
    users: []
  };

  usersList = () => {
    this.setState(state => ({
      usersList: !state.usersList
    }))

    if (!this.state.usersList) {
      axios.defaults.xsrfHeaderName = "X-CSRFTOKEN";
      axios.defaults.xsrfCookieName = "csrftoken";
      axios.get(`${HOST_URL}/api/broadcasts/${this.props.chatID}/watchers/`, {
        headers: {
          "Content-Type": "application/json"
        }
      })
        .then(res => {
          console.log(res.data.result)
          this.setState({
            loading: false,
            users: res.data.result
          })
        }).catch(err => console.log("error " + err));
    }
  }

  render() {
    let users = this.state.users;

    return (
      <>
        { (this.state.usersList) ? (
          <div className="messages">
            <List>
              {/* TODO: usersList –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –≤ Redux state, –¥–ª—è Search —Å–¥–µ–ª–∞—Ç—å –æ—Ç–¥–µ–ª—å–Ω—ã–π —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç */}
              <br/>
                <Search style={{textAlign: 'center'}}/>
              <br/>

              {Object.keys(users).map((role, i) =>
                <List.Item key={i}>
                  <List.Header content={rolesAdapt[role]} />

                  <List>
                    {users[role].map((user) => (
                      <List.Item
                        key={user.id}
                        content={user.username}
                        className='userLink'
                        style={{color: '#6441a5'}}
                        as='a'
                      />
                    ))}
                  </List>

                  <br/>
                </List.Item>
              )}
            </List>

          </div> ) : <div/> }

        <div className="chatHeader">
          <Button
            content={this.props.watchers}
            onClick={this.usersList}
            icon={'users'}
            basic
            compact
          />

          <Dropdown
            trigger={
              <span>
                <Image avatar src='https://static-cdn.jtvnw.net/jtv_user_pictures/panel-148316617-image-3036e147-20a6-490c-ae31-9cfa6cdd73ad' />
                {this.props.username}
              </span>
            }
            pointing='top right'
            icon={"dropdown"}
            style={{
              float: 'right', height: '38px', margin: '-5px 10px 0 0',
              padding: '8px 11px 0 11px', background: '#EFEFEF',
              fontWeight: 'normal', fontSize: '14px', borderRadius: '13px'
            }}
          >
            <Dropdown.Menu>
              <Dropdown.Item>
                <Icon name='user'/>
                –ê–∫–∫–∞—É–Ω—Ç
              </Dropdown.Item>

              <Dropdown.Item>
                <Icon name='settings'/>
                –ù–∞—Å—Ç—Ä–æ–π–∫–∏
              </Dropdown.Item>

              <Dropdown.Item onClick={() => this.props.logout()}>
                <Icon name='sign out'/>
                –í—ã—Ö–æ–¥
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    watchers: state.message.watchers,
    username: state.auth.username
  };
};

const mapDispatchToProps = dispatch => {
  return {
    logout: () => dispatch(logout())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ChatHeader);